import React from 'react';
import SwitchInput from '../SwitchInput';

/** function: BannerContainer */
const BannerContainer = () => {
    const styles = {
        quoterContainer: {
            height: '452px',
            background: '#EEEEEF',
            width: '744px',
            margin: '38px auto',
            padding: '29px 30px 0px',
            textAlign: 'center',
            marginBottom: '110px'
        },
        header: {
            fontSize: '31px',
            color: '#514E4E',
            fontFamily: 'Helvetica',
            fontWeight: 'lighter',
            marginBottom: '30px'
        },
        select: {
            height: '52px',
            display: 'block',
            width: '100%'
        },
        inputGroup: {
            marginBottom: '1.3rem'
        },
        birthDateGroup: {
          header: {
            display: 'block',
            width: '100%',
            fontWeight: '300',
            fontSize: '1.5em',
            lineHeight: '1.0',
            textTransform: 'none',
            textAlign: 'center',
            color: '#2a2a2a',
          }
        },
        oneThirdGroup: {
            width: '32%',
            marginLeft: '1px',
            marginRight: '3px'
        },
        needsAnalyzer: {
            button: {
                background: '#5CB85C',
                border: '2px solid #5CB85C',
                height: '52px'
            }
        },
        quoteFooter: {
            fontFamily: 'Helvetica',
            fontWeight: 'lighter',
            fontSize: '15px'
        },
        btn: {
            width: '100%'
        }
    };

    const benefitGroup = (<div className="input-group mb-3" style={styles.inputGroup}>
                            <select className="custom-select form-input" style={styles.select}>
                                <option selected="">
                                    Benefit Amount
                                </option>
                                <option value="1">
                                    One
                                </option>
                                <option value="2">
                                    Two
                                </option>
                                <option value="3">
                                    Three
                                </option>
                            </select>
                        </div>);

    const birthDateGroup = ( <div className="input-group mb-3" style={styles.inputGroup}>
            <br />
            <div>
                <h3 style={styles.birthDateGroup.header}>Birthdate: </h3>
            </div>
            <br />
            <div className="div-one-third" style={styles.oneThirdGroup}>
                <select className="custom-select third-select" id="inputGroupSelect01" style={styles.select}>
                    <option selected="">
                        Month
                    </option>
                    <option value="1">
                        One
                    </option>
                    <option value="2">
                        Two
                    </option>
                    <option value="3">
                        Three
                    </option>
                </select>
            </div>

            <div className="div-one-third" style={styles.oneThirdGroup}>
                <select className="custom-select third-select" id="inputGroupSelect01" style={styles.select}>
                    <option selected="">
                        Day
                    </option>
                    <option value="1">
                        One
                    </option>
                    <option value="2">
                        Two
                    </option>
                    <option value="3">
                        Three
                    </option>
                </select>
            </div>

            <div className="div-one-third" style={styles.oneThirdGroup}>
                <select className="custom-select third-select" id="inputGroupSelect01" style={styles.select}>
                    <option selected="">
                        Year
                    </option>
                    <option value="1">
                        One
                    </option>
                    <option value="2">
                        Two
                    </option>
                    <option value="3">
                        Three
                    </option>
                </select>
            </div>

        </div>);

    const NeedsAnalyzer = (
        <div className="input-group mb-3" style={styles.inputGroup}>
            <a href="#" id="aq2e_send" target="_blank" style={Object.assign({}, styles.btn, styles.needsAnalyzer.button)}
               className="btn btn-lg btn-primary fill-box-lg fill-parent">Needs Analyzer</a>
        </div>
    );

    const TermsGroup = (
        <div className="input-group mb-3" style={styles.inputGroup}>
            <select className="custom-select" id="inputGroupSelect01" style={styles.select}>
                <option selected="">
                    Term Years
                </option>
                <option value="1">
                    One
                </option>
                <option value="2">
                    Two
                </option>
                <option value="3">
                    Three
                </option>
            </select>
        </div>
    );

    const statesGroup = (
        <div className="input-group mb-3" style={styles.inputGroup}>
            <select className="custom-select" id="inputGroupSelect01" style={styles.select}>
                <option selected="">
                    California
                </option>
                <option value="1">
                    One
                </option>
                <option value="2">
                    Two
                </option>
                <option value="3">
                    Three
                </option>
            </select>
        </div>
    );

    const premiumGroup = (
        <div className="input-group mb-3" style={styles.inputGroup}>
            <select className="custom-select" id="inputGroupSelect01" style={styles.select}>
                <option selected="">
                    Premium
                </option>
                <option value="1">
                    One
                </option>
                <option value="2">
                    Two
                </option>
                <option value="3">
                    Three
                </option>
            </select>
        </div>
    );

    const footer = (
        <div className="quote-footer"
             style={styles.quoteFooter}>
            By clicking the "Get A Quote" button I give consent for a licensed agent to call me.
        </div>
    );

    return (
        <div className="quote-container"
             style={styles.quoterContainer}>
            <h2 style={styles.header}>Get
                An Instant Quote Below</h2>
            <div className="row">
                <div className="col-md-6">

                    { benefitGroup }
                    { birthDateGroup }

                    <SwitchInput
                        radioName="gender"

                        firstHtmlId="sex-male"
                        firstLabel="Male"
                        firstRadioClassName="switch-input"
                        firstLabelClassName="switch-label form-control switch-right"
                        firstValue="M"

                        secondHtmlId="sex-female"
                        secondLabel="Female"
                        secondRadioClassName="switch-input"
                        secondLabelClassName="switch-label form-control switch-left"
                        secondValue="F"

                        onChange={() => {}}

                        defaultSwitch="first"
                    />

                    { NeedsAnalyzer }

                </div>
                <div className="col-md-6">
                    
                    { TermsGroup }
                    { statesGroup }
                    { premiumGroup }

                    <SwitchInput
                        radioName="tobacco"

                        firstHtmlId="tobacco-yes"
                        firstLabel="Tobacco"
                        firstRadioClassName="switch-input"
                        firstLabelClassName="switch-label form-control switch-right"
                        firstValue="N"

                        secondHtmlId="tobacco-no"
                        secondLabel="Non Tobacco"
                        secondRadioClassName="switch-input"
                        secondLabelClassName="switch-label form-control switch-left"
                        secondValue="Y"

                        onChange={() => {}}

                        defaultSwitch="first"
                    />

                    <a href="#" id="aq2e_send" target="_blank" style={styles.btn}
                        className="btn btn-lg btn-primary fill-box-lg fill-parent">Get A Quote</a>
                    
                </div>
                <div className="col-md-12">
                    { footer }
                </div>
            </div>
        </div>
    );
}

export default BannerContainer;


