import React from 'react';
import PropTypes from 'prop-types';
import Label from '../Label';

/** Text input with integrated label to enforce consistency in layout, error display, label placement  */
const TextInput = ({
        name,
        label,
        placeholder,
        onChange,
        value,
        required,
        error,
        ...props
    }) => {
    let classNames = {
        wrapperClass: '',
        alert: 'alert alert-danger',
    };

    let css = {
        for: {
            container: {
                marginBottom: '16px'
            },
            input: {
                border: 'solid 1px red'
            },
            error: {
                color: 'red'
            }
        }
    };

    classNames.wrapperClass = error && error.length > 0 ? 'form-group has-error' : 'form-group';

    return (
        <div className={classNames.wrapperClass}>
            <Label htmlFor={name} label={label} required />
            <div className="field">
                <input
                    id={name}
                    className="form-control"
                    name={name}
                    onChange={onChange}
                    placeholder={placeholder}
                    value={value}
                    required
                    style={error && css.for.input}
                    {...props}
                />
                {error && <div className={classNames.alert} style={css.for.error}>{error}</div>}
            </div>
        </div>
    );
};

TextInput.propTypes = {
    /** Input name. Unique HTML ID. Recommend setting this to match object's property so a single change handler can */
    name: PropTypes.string.isRequired,

    /** Input label  */
    label: PropTypes.string.isRequired,

    /** Mark label with asterisk if set to true */
    required: PropTypes.bool,

    /** Function to call onChange */
    onChange: PropTypes.func.isRequired,

    /** Placeholder to display when empty */
    placeholder: PropTypes.string,

    /** Value  */
    value: PropTypes.any,

    /** String to display when error occurs.  */
    error: PropTypes.string,

    /** Child component to display next to input  */
    children: PropTypes.node
};

export default TextInput;


