let savedCategories = [];

class CategoryAPI {
    static getAllCategories() {
        return new Promise((resolve, reject) => {
            setTimeout(() => {
              //  console.log(['Categories'], categories);
                resolve(Object.assign([], categories))
            }, delay);
        })
    }
}

export default CategoryAPI;


