import React from 'react';
import PropTypes from 'prop-types';
import SelectInput from '../SelectInput';
import CheckBoxInput from '../CheckBoxInput';
import Error from '../Error';
import ContentForm from "../common/ContentForm";

/** function: MicrositeForm */
const MicrositeForm = ({ error }) => {
    // console.log('CourseForm', [course, allAuthors, onSave, onChange, saving, errors])

    const options = [
        {text: 'Term Life', value: '0'},
        {text: 'Simplified Issue Term', value: '1'},
        {text: 'Simplified Issue Whole Life', value: '2'},
        {text: 'Guaranteed Universal Life', value: '3'}
    ];

    return (
        <ContentForm>
            <h1>Microsite Details</h1>
            <Error error={""} />
            <SelectInput
                label="Default Category"
                name="category"
                defaultOption="Term Life"
                defaultValue={'0'}
                required
                options={options}
            />

            <CheckBoxInput
                htmlId="test"
                name="test"
                label="Use Logo"
                className="radio"
                value="1"
            />

            <CheckBoxInput
                htmlId="test"
                name="test"
                label="Use Portrait"
                className="radio"
                value="1"
            />

            <br />

        </ContentForm>
    );
};

MicrositeForm.propTypes = {};
MicrositeForm.defaultProps = {};

export default MicrositeForm;


