// This component handles the App template used on every page
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Header from './common/Header';
import { connect } from 'react-redux';
import { Route } from "react-router-dom";
import HomePage from "./HomePage/HomePage";
import AboutPage from "./AboutPage/AboutPage";
import CoursesPage from "./course/CoursesPage";
import ManageCoursePage from "./course/ManageCoursePage";
import UserPage from "./user/UserPage";
import PasswordPage from "./user/PasswordPage";
import ProfilePage from "./user/ProfilePage";
import MicrositePage from "./microsite/MicrositePage";
import CarrierPage from "./carrier/CarrierPage";
import AccountPage from "./user/AccountPage";
import BannerPage from "./banner/BannerPage";

class App extends Component {

    render() {
        console.log(['App::props'], this.props);
        return (
            <div className="container-fluid">
                <Header
                    loading={this.props.loading}
                />

                <Route exact path="/" component={HomePage}/>
                <Route path="/courses" component={CoursesPage}/>
                <Route path="/course/:id" component={ManageCoursePage}/>
                <Route path="/course" component={ManageCoursePage} exact />
                <Route path="/user" component={UserPage} exact />
                <Route path="/password" component={PasswordPage} exact />
                <Route path="/profile" component={ProfilePage} exact />
                <Route path="/microsite" component={MicrositePage} exact />
                <Route path="/carriers" component={CarrierPage} exact />
                <Route path="/account" component={AccountPage} />
                <Route path="/banner" component={BannerPage} />

                {this.props.children}
            </div>
        )
    }
}

App.propTypes = {
    /** get the children that will be loaded in the main container */
    children: PropTypes.object,

    /** must get the loading value so we know when to load the dots */
    loading: PropTypes.bool.isRequired
};

const mapStateToProps = (state, ownProps) => {
  return {
      loading: state.ajaxCallsInProgress > 0
  };
};

export default connect(mapStateToProps)(App);


